import { React, useState } from 'react';
import { Link } from 'react-router-dom';
import ContactForm from './ContactForm';

function ContactUsPage() {
    const [formSubmitted, setFormSubmitted] = useState(false);

    return (
        <div className='min-h-screen sm:py-16 xl:py-0 xl:px-20 bg-white sm:bg-lightgray font-quicksand flex justify-center gap-x-20'>
            <div className='hidden xl:flex w-7/12 relative'>
                <Link className='text-2xl absolute top-10' to='/'>T2 Codes</Link>
                <img className='w-5/6 mx-auto' src={require('./../assets/support.svg').default} />
            </div>
            <div className='max-w-lg xl:max-w-none xl:w-5/12 bg-white sm:shadow-md p-10 sm:p-16 xl:h-screen xl:py-32'>
                <div className='xl:hidden mb-8 text-sm text-rose hover:underline'><Link to='/'>Back Home</Link></div>
                {!formSubmitted ? <ContactForm setFormSubmitted={setFormSubmitted} />
                    : <div>
                        <h3 className='text-2xl font-noto-sans font-semibold'>Message Delivered</h3>
                        <p className='mt-3'>Our team will get back to you soon :)</p>
                    </div>
                }
            </div>
        </div>
    )
}

export default ContactUsPage;