import { React, useState } from 'react';
import { Link } from 'react-router-dom';
import validateEmail from '../utils/validateEmail';
import { ArrowRightIcon } from '@heroicons/react/solid';
import { HashLink } from 'react-router-hash-link';

function Footer(props) {
    const [email, setEmail] = useState('');
    const [emailError, setEmailError] = useState('');

    const handleSubmit = e => {
        e.preventDefault();
        if (email !== '' && validateEmail(email)) {
            setEmail('');
            setEmailError('');
            props.showToast('You have been added successfully to our newsletter');
        } else {
            setEmailError('Email is invalid');
        }
    }

    return (
        <div className='border-t border-mediumgray mt-16 lg:mt-24 px-10 pt-10 pb-5 lg:pb-16 flex flex-col lg:flex-row gap-x-8 font-quicksand text-sm'>
            <div className='hidden lg:block w-3/12'>
                <p className='text-2xl'>T2 Codes</p>
                <p className='mt-1'>©T 2 Codes, Poland</p>
            </div>
            <div className='w-full lg:w-5/12 flex'>
                <div className='w-3/6'>
                    <span className='font-semibold'>T2 Codes</span>
                    <ul>
                        <li className='mt-2'>
                            <Link to='/' className='hover:underline'>Home</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='/contact' className='hover:underline'>Contact</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='/login' className='hover:underline'>Login</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='/register' className='hover:underline'>Register</Link>
                        </li>
                    </ul>
                </div>
                <div className='w-3/6'>
                    <span className='font-semibold'>Policies</span>
                    <ul>
                        <li className='mt-2'>
                            <HashLink to='/policies#privacyPolicy' className='hover:underline'>Privacy Policy</HashLink>
                        </li>
                        <li className='mt-2'>
                            <HashLink to='/policies#termsOfUseAndSales' className='hover:underline'>Terms of Use and Sales</HashLink>
                        </li>
                        <li className='mt-2'>
                            <HashLink to='/policies#refundPolicy' className='hover:underline'>Refund Policy</HashLink>
                        </li>
                    </ul>
                </div>
            </div>
            <div className='w-full md:w-4/6 lg:w-4/12 my-10 lg:my-0 mx-auto text-center'>
                <p className='font-semibold mb-2'>Join Our Newsletter</p>
                <p className='text-sm text-left text-red mb-1'>
                    { emailError }
                </p>
                <form className='relative' onSubmit={handleSubmit}>
                    <input
                        placeholder='Email Address'
                        className='w-full h-10 pl-5 pr-10 rounded-lg bg-lightgray focus:outline-none'
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                    />
                    <button className='bg-transparent absolute top-3 right-3 w-4 h-4'>
                        <ArrowRightIcon />
                    </button>
                </form>
            </div>
            <div className='lg:hidden text-center text-xs'>
                ©T 2 Codes, Poland
            </div>
        </div>
    );
}

export default Footer;