import './App.css';
import { HashRouter as Router, Routes, Route } from "react-router-dom"
import { HomePage, LoginPage, RegisterPage, ContactUsPage, PoliciesPage, ConfirmCookies, Toast } from './components';

function App() {

    const showToast = (msg) => {
        // extract toast div for ease of use
        const toastDiv = document.getElementById('toastDiv');

        // set toast message
        document.getElementById('toastMsg').innerHTML = msg; 

        // show the toast div
        // Note: Changes in the opacity of toastDiv take 300ms for the transition
        toastDiv.classList.replace('hidden', 'flex');
        toastDiv.classList.replace('opacity-0', 'opacity-100');

        // after 300ms + 3000ms hide the toast div
        setTimeout(() => {
            toastDiv.classList.replace('opacity-100', 'opacity-0');

            setTimeout(() => {
                // hide toast div after opacity transition to 0
                toastDiv.classList.replace('flex', 'hidden');

                // clear toast message
                document.getElementById('toastMsg').innerHTML = '';
            }, 300);
        }, 3300);
    }

    return (
        <Router>
            <Routes>
                <Route
                    path='/'
                    element={<HomePage showToast={showToast} />}
                />
                <Route
                    path='/login'
                    element={<LoginPage showToast={showToast} />}
                />
                <Route
                    path='/register'
                    element={<RegisterPage showToast={showToast} />}
                />
                <Route
                    path='/contact'
                    element={<ContactUsPage />}
                />
                <Route
                    path='/policies'
                    element={<PoliciesPage showToast={showToast} />}
                />
            </Routes>
            <Toast />
            <ConfirmCookies />
        </Router>
    );
}

export default App;
