import React from 'react';
import { CheckCircleIcon } from '@heroicons/react/solid';

function Toast() {
    return (
        <div id="toastDiv" className="hidden opacity-0 w-80 fixed top-5 left-1/2 -translate-x-1/2 items-center bg-green border-l-4 border-darkgreen text-white py-3 px-6 shadow-md transition-opacity duration-300">
            <CheckCircleIcon width='1.8em' height='1.8em' />
            <div id="toastMsg" className="ml-4"></div>
        </div>
    )
}

export default Toast;