import { React, useState } from 'react';
import { Link } from 'react-router-dom';

import { MenuAlt3Icon, XIcon } from '@heroicons/react/solid';

function Navbar() {
    const [navbarHidden, setNavbarHidden] = useState(true);

    return (
        <div id='navbar' className='flex flex-wrap md:flex-nowrap justify-between mt-10 font-quicksand'>
            <div className='text-2xl'><Link to='/'>T2 Codes</Link></div>
            <button
                className='md:hidden hover:text-rose h-6 w-6 cursor-pointer'
                onClick={() => setNavbarHidden(!navbarHidden)}
            >
                {navbarHidden ? <MenuAlt3Icon /> : <XIcon />}
            </button>
            <ul className={(navbarHidden ? 'hidden' : 'flex') + ' md:flex justify-between md:gap-x-20 w-full md:w-auto mt-5 md:mt-0 py-3 md:py-0 px-10 md:px-0 bg-lightgray md:bg-transparent text-sm md:text-base rounded-lg'}>
                <li className='my-auto'>
                    <Link to='/contact' className="hover:text-rose">Contact</Link>
                </li>
                <li className='my-auto'>
                    <Link to='/login' className="hover:text-rose">Login</Link>
                </li>
                <li className='my-auto'>
                    <Link to='/register' className="hover:text-rose">Register</Link>
                </li>
            </ul>
        </div>
    )
}

export default Navbar;