import { React, useState} from 'react';
import validateEmail from '../utils/validateEmail';

const ContactForm = (props) => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [topic, setTopic] = useState('0');
    const [message, setMessage] = useState('');
    const [nameError, setNameError] = useState('');
    const [emailError, setEmailError] = useState('');
    const [topicError, setTopicError] = useState('');
    const [messageError, setMessageError] = useState('');

    const validateUserInput = () => {
        let errorFound = false;

        if (name === '') {
            setNameError('Name cannot be empty');
            errorFound = true;
        } else {
            setNameError('');
        }

        if (email === '' || !validateEmail(email)) {
            setEmailError('Email is invalid');
            errorFound = true;
        } else {
            setEmailError('');
        }

        if (topic === '0') {
            setTopicError('Please select a topic');
            errorFound = true;
        } else {
            setTopicError('');
        }

        if (message === '') {
            setMessageError('Message cannot be empty');
            errorFound = true;
        } else {
            setMessageError('');
        }

        return !errorFound;
    }

    const handleSubmit = e => {
        e.preventDefault();
        if (validateUserInput()) {
            props.setFormSubmitted(true);
            console.log({ name, email, topic, message });
        }
    }

    return (
        <div>
            <h3 className='text-2xl font-noto-sans font-semibold'>Contact Us</h3>
            <p className='mt-3'>Rest assured! Our customer support service will respond you at the earliest.</p>
            <form className='mt-5' onSubmit={handleSubmit}>
                <div className='mt-3'>
                    <p className='text-sm text-red'>
                        {nameError}
                    </p>
                    <input value={name} onChange={e => setName(e.target.value)} className='w-full border-b border-rose pt-2 pb-5 placeholder-black focus:outline-none' placeholder='Name' />
                </div>
                <div className='mt-3'>
                    <p className='text-sm text-red'>
                        {emailError}
                    </p>
                    <input value={email} onChange={e => setEmail(e.target.value)} className='w-full border-b border-rose pt-2 pb-5 placeholder-black focus:outline-none' placeholder='Email' />
                </div>
                <div className='mt-3'>
                    <p className='text-sm text-red'>
                        {topicError}
                    </p>
                    <select value={topic} onChange={e => setTopic(e.target.value)} className='w-full border-b bg-white border-rose pt-2 pb-5 focus:outline-none'>
                        <option value='0'>Select Topic</option>
                        <option value='1'>Order Booking System</option>
                        <option value='2'>Appointments System</option>
                        <option value='3'>Visitor Queue Management System</option>
                        <option value='4'>Website Builder</option>
                        <option value='5'>Receipts Vouchers/Invoicing System</option>
                        <option value='6'>Any other Product/Query</option>
                    </select>
                </div>
                <div className='mt-3'>
                    <p className='text-sm text-red'>
                        {messageError}
                    </p>
                    <input value={message} onChange={e => setMessage(e.target.value)} className='w-full pt-2 pb-5 placeholder-black focus:outline-none' placeholder='Message' />
                </div>
                <div className='text-center'>
                    <button className='mt-10 bg-rose hover:bg-darkrose font-bold text-white px-12 py-3 rounded-full'>
                        Submit
                    </button>
                </div>
            </form>
        </div>
    )
}

export default ContactForm;